export default "import React, {useState} from 'react';\nimport styled from '@emotion/styled';\nimport {DragDropContext, Draggable, Droppable} from 'react-beautiful-dnd';\nimport simpleListData from '../../../../@crema/services/db/extraPages/dndData/simpleListData';\nimport ListItemAvatar from '@material-ui/core/ListItemAvatar';\nimport Avatar from '@material-ui/core/Avatar';\nimport ListItemText from '@material-ui/core/ListItemText';\nimport ListItem from '@material-ui/core/ListItem';\nimport Box from '@material-ui/core/Box';\nimport {grey} from '@material-ui/core/colors';\nimport {Fonts} from '../../../../shared/constants/AppEnums';\nimport {makeStyles} from '@material-ui/core';\n\nconst grid = 8;\nconst reorder = (list, startIndex, endIndex) => {\n  const result = Array.from(list);\n  const [removed] = result.splice(startIndex, 1);\n  result.splice(endIndex, 0, removed);\n\n  return result;\n};\n\nconst useStyles = makeStyles((theme) => {\n  return {\n    root: {\n      [theme.breakpoints.down('xs')]: {\n        width: '100%',\n        '& .css-1gwkzgr': {\n          width: '100%',\n        },\n      },\n    },\n    rootList: {\n      backgroundColor: theme.palette.background.paper,\n    },\n  };\n});\n\nconst QuoteItem = styled.div`\n  width: 170px;\n  margin-bottom: 20px;\n  background-color: #e0e0e0;\n  padding: ${grid}px;\n  @media (min-width: 600px) {\n    width: 300px;\n  }\n`;\n\nfunction Quote({quote, index}) {\n  const classes = useStyles();\n  return (\n    <Draggable draggableId={quote.handle} index={index}>\n      {(provided) => (\n        <QuoteItem\n          ref={provided.innerRef}\n          {...provided.draggableProps}\n          {...provided.dragHandleProps}>\n          <Box p={0} clone>\n            <ListItem className={classes.rootList}>\n              <Box\n                mr={3}\n                mt={0}\n                ml={1.5}\n                display='flex'\n                justifyContent='center'\n                clone>\n                <ListItemAvatar>\n                  <Avatar alt='Remy Sharp' src={quote.image} />\n                </ListItemAvatar>\n              </Box>\n\n              <Box my={0} clone>\n                <ListItemText\n                  primary={\n                    <Box component='span' fontWeight={Fonts.MEDIUM}>\n                      {quote.name}\n                    </Box>\n                  }\n                  secondary={\n                    <Box\n                      component='span'\n                      display='block'\n                      color='text.secondary'\n                      overflow='hidden'\n                      textOverflow='ellipsis'\n                      whiteSpace='nowrap'>\n                      @{quote.handle}\n                    </Box>\n                  }\n                />\n              </Box>\n            </ListItem>\n          </Box>\n        </QuoteItem>\n      )}\n    </Draggable>\n  );\n}\n\nconst QuoteList = React.memo(function QuoteList({quotes}) {\n  return quotes.map((quote, index) => (\n    <Quote quote={quote} index={index} key={quote.id} />\n  ));\n});\n\nconst WithHooks = () => {\n  const [state, setState] = useState({quotes: simpleListData});\n  const classes = useStyles();\n\n  function onDragEnd(result) {\n    if (!result.destination) {\n      return;\n    }\n\n    if (result.destination.compose === result.source.compose) {\n      return;\n    }\n\n    const quotes = reorder(\n      state.quotes,\n      result.source.index,\n      result.destination.index,\n    );\n\n    setState({quotes});\n  }\n\n  return (\n    <DragDropContext onDragEnd={onDragEnd}>\n      <Box width={1} display='flex' flexDirection='column' alignItems='center'>\n        <Box\n          className={classes.root}\n          border={`solid 4px ${grey[300]}`}\n          p={2}\n          mb={3}>\n          <Droppable droppableId='list'>\n            {(provided) => (\n              <Box ref={provided.innerRef} {...provided.droppableProps}>\n                <QuoteList quotes={state.quotes} />\n                {provided.placeholder}\n              </Box>\n            )}\n          </Droppable>\n        </Box>\n      </Box>\n    </DragDropContext>\n  );\n};\nexport default WithHooks;\n";