export default "import React, {Component} from 'react';\nimport {DragDropContext, Draggable, Droppable} from 'react-beautiful-dnd';\nimport ListItemAvatar from '@material-ui/core/ListItemAvatar';\nimport Avatar from '@material-ui/core/Avatar';\nimport ListItemText from '@material-ui/core/ListItemText';\nimport ListItem from '@material-ui/core/ListItem';\nimport simpleListData from '../../../../@crema/services/db/extraPages/dndData/simpleListData';\nimport Box from '@material-ui/core/Box';\nimport {Fonts} from '../../../../shared/constants/AppEnums';\nimport withStyles from '@material-ui/core/es/styles/withStyles';\n\n// a little function to help us with reordering the result\nconst reorder = (list, startIndex, endIndex) => {\n  const result = Array.from(list);\n  const [removed] = result.splice(startIndex, 1);\n  result.splice(endIndex, 0, removed);\n\n  return result;\n};\n\nconst grid = 8;\n\nconst styles = (theme) => {\n  return {\n    rootList: {\n      backgroundColor: theme.palette.background.paper,\n    },\n  };\n};\n\nconst getListStyle = (isDraggingOver) => ({\n  // background: isDraggingOver ? 'lightblue' : 'lightgrey',\n  border: '5px solid #E0E0E0',\n  display: 'flex',\n  padding: grid,\n  overflow: 'auto',\n});\n\nclass Horizontal extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      items: simpleListData.slice(0, 6),\n    };\n    this.onDragEnd = this.onDragEnd.bind(this);\n  }\n\n  onDragEnd(result) {\n    // dropped outside the list\n    if (!result.destination) {\n      return;\n    }\n\n    const items = reorder(\n      this.state.items,\n      result.source.index,\n      result.destination.index,\n    );\n\n    this.setState({\n      items,\n    });\n  }\n\n  // Normally you would want to split things out into separate core.\n  // But in this example everything is just done in one place for simplicity\n  render() {\n    const {classes} = this.props;\n    return (\n      <DragDropContext onDragEnd={this.onDragEnd}>\n        <Droppable droppableId='droppable' direction='horizontal'>\n          {(provided, snapshot) => (\n            <div\n              ref={provided.innerRef}\n              style={getListStyle(snapshot.isDraggingOver)}\n              {...provided.droppableProps}>\n              {this.state.items.map((item, index) => (\n                <Draggable\n                  key={item.id}\n                  draggableId={item.handle}\n                  index={index}>\n                  {(provided, snapshot) => (\n                    <Box\n                      mr={3}\n                      ref={provided.innerRef}\n                      {...provided.draggableProps}\n                      {...provided.dragHandleProps}>\n                      <Box p={2} width='16rem' mr={2} clone>\n                        <ListItem className={classes.rootList}>\n                          <Box\n                            mr={3}\n                            mt={0}\n                            ml={1.5}\n                            display='flex'\n                            justifyContent='center'\n                            clone>\n                            <ListItemAvatar>\n                              <Avatar alt='Remy Sharp' src={item.image} />\n                            </ListItemAvatar>\n                          </Box>\n                          <Box my={0} clone>\n                            <ListItemText\n                              primary={\n                                <Box component='span' fontWeight={Fonts.MEDIUM}>\n                                  {item.name}\n                                </Box>\n                              }\n                              secondary={\n                                <Box\n                                  component='span'\n                                  display='block'\n                                  color='text.secondary'>\n                                  @{item.handle}\n                                </Box>\n                              }\n                            />\n                          </Box>\n                        </ListItem>\n                      </Box>\n                    </Box>\n                  )}\n                </Draggable>\n              ))}\n              {provided.placeholder}\n            </div>\n          )}\n        </Droppable>\n      </DragDropContext>\n    );\n  }\n}\n\nexport default withStyles(styles)(Horizontal);\n";