export default "import React, {Component} from 'react';\nimport PropTypes from 'prop-types';\nimport {DragDropContext, Draggable, Droppable} from 'react-beautiful-dnd';\nimport simpleListData from '../../../../@crema/services/db/extraPages/dndData/simpleListData';\nimport ListItemAvatar from '@material-ui/core/ListItemAvatar';\nimport Avatar from '@material-ui/core/Avatar';\nimport ListItemText from '@material-ui/core/ListItemText';\nimport ListItem from '@material-ui/core/ListItem';\nimport Box from '@material-ui/core/Box';\nimport {Fonts} from '../../../../shared/constants/AppEnums';\nimport withStyles from '@material-ui/core/es/styles/withStyles';\n\n// a little function to help us with reordering the result\nconst reorder = (list, startIndex, endIndex) => {\n  const result = Array.from(list);\n  const [removed] = result.splice(startIndex, 1);\n  result.splice(endIndex, 0, removed);\n  return result;\n};\n\nconst grid = 8;\n\nconst styles = (theme) => {\n  return {\n    rootList: {\n      backgroundColor: theme.palette.background.paper,\n    },\n  };\n};\n\nconst getListStyle = (isDraggingOver, overflow) => ({\n  // background: isDraggingOver ? 'lightblue' : 'grey',\n  border: '5px solid pink',\n  padding: grid,\n  maxHeight: '50vh',\n  overflow,\n  width: 300,\n  '@media (min-width: 600px)': {\n    width: 300,\n  },\n});\n\nclass Simple extends Component {\n  static propTypes = {\n    overflow: PropTypes.string,\n  };\n  static defaultProps = {\n    overflow: 'auto',\n  };\n\n  constructor(props, context) {\n    super(props, context);\n    this.state = {\n      items: simpleListData,\n    };\n    this.onDragEnd = this.onDragEnd.bind(this);\n  }\n\n  onDragEnd(result) {\n    // dropped outside the list\n    if (!result.destination) {\n      return;\n    }\n\n    const items = reorder(\n      this.state.items,\n      result.source.index,\n      result.destination.index,\n    );\n\n    this.setState({\n      items,\n    });\n  }\n\n  // Normally you would want to split things out into separate core.\n  // But in this example everything is just done in one place for simplicity\n  render() {\n    const {classes} = this.props;\n    return (\n      <DragDropContext onDragEnd={this.onDragEnd}>\n        <Droppable droppableId='droppable'>\n          {(droppableProvided, droppableSnapshot) => (\n            <Box display='flex' flexDirection='column' alignItems='center'>\n              <Box\n                ref={droppableProvided.innerRef}\n                style={getListStyle(\n                  droppableSnapshot.isDraggingOver,\n                  this.props.overflow,\n                )}\n                onScroll={\n                  (e) => {}\n                  // eslint-disable-next-line no-console\n                }>\n                {this.state.items.map((item, index) => (\n                  <Draggable\n                    key={item.id}\n                    draggableId={item.handle}\n                    index={index}>\n                    {(draggableProvided, draggableSnapshot) => (\n                      <Box\n                        ref={draggableProvided.innerRef}\n                        {...draggableProvided.draggableProps}\n                        {...draggableProvided.dragHandleProps}>\n                        <Box px={2} mb={5} clone>\n                          <ListItem className={classes.rootList}>\n                            <Box\n                              mr={3}\n                              mt={0}\n                              display='flex'\n                              justifyContent='center'\n                              clone>\n                              <ListItemAvatar>\n                                <Avatar alt='Remy Sharp' src={item.image} />\n                              </ListItemAvatar>\n                            </Box>\n                            <Box my={0} clone>\n                              <ListItemText\n                                primary={\n                                  <Box\n                                    component='span'\n                                    fontWeight={Fonts.MEDIUM}>\n                                    {item.name}\n                                  </Box>\n                                }\n                                secondary={\n                                  <Box\n                                    component='span'\n                                    display='block'\n                                    color='text.secondary'\n                                    overflow='hidden'\n                                    textOverflow='ellipsis'\n                                    whiteSpace='nowrap'>\n                                    @{item.handle}\n                                  </Box>\n                                }\n                              />\n                            </Box>\n                          </ListItem>\n                        </Box>\n                      </Box>\n                    )}\n                  </Draggable>\n                ))}\n                {droppableProvided.placeholder}\n              </Box>\n            </Box>\n          )}\n        </Droppable>\n      </DragDropContext>\n    );\n  }\n}\n\nexport default withStyles(styles)(Simple);\n";